@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600&display=swap);
body {
    font-family: 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --primary-color: #876e00;
    --gray-color-1: #7f7f7f4d;
    --gray-color-2: #999;
    --gray-color-3: var(--gray-color-5);
    --gray-color-4: #666;
    --gray-color-5: #e6e6e699;
    --gray-color-6: #ddd;
    --gray-color-7: #ccc;
    --white-color: #fff;
    --green-color-1: #70ed42;
    --red-color-1: #ed4250;
}


body {
    padding: 0;
}

body::-webkit-scrollbar {
    width: .3em;
}

body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px var(--gray-color-1);
}

body::-webkit-scrollbar-thumb {
    background-color: var(--primary-color);
    outline: 1px solid slategrey;
}

body:before {
    background-color: #ffffff;
    background-image: linear-gradient(135deg, #f7d745 25%, transparent 25%), linear-gradient(225deg, #f7d745 25%, transparent 25%), linear-gradient(45deg, #f7d745 25%, transparent 25%), linear-gradient(315deg, #f7d745 25%, #ffffff 25%);
    background-position: 16px 0, 16px 0, 0 0, 0 0;
    background-repeat: repeat;
    background-size: 32px 32px;
    bottom: 0;
    content: '';
    left: 0;
    opacity: 0.3;
    position: fixed;
    right: 0;
    top: 0;
    z-index: -1;
}

.App {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
    position: relative;
}

.app-blankstate {
    -webkit-align-items: center;
            align-items: center;
    color: var(--primary-color);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    -webkit-flex-basis: calc(100% - 150px);
            flex-basis: calc(100% - 150px);
    -webkit-flex-direction: column;
            flex-direction: column;
    font-size: 32px;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    text-shadow: 1px 1px 30px var(--gray-color-2);
}

header {
    -webkit-align-items: center;
            align-items: center;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    background: var(--gray-color-5);
    border-bottom: 1px solid var(--gray-color-1);
    border-top: 1px solid var(--gray-color-1);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 0 0 20px;
    padding: 12px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

h1 {
    font-family: 'Indie Flower', cursive;
    font-size: 18px;
    line-height: 0;
    margin: 0;
}

header span {
    color: var(--primary-color);
    font-size: 13px;
    margin: 0;
    text-transform: uppercase;
}

header strong {
    font-size: 15px;
}

.undo-redo button {
    background: transparent;
    border: none;
}

.undo-redo button:enabled {
    cursor: pointer;
}

.undo-redo button:hover:enabled {
    color: var(--primary-color);
}

footer {
    -webkit-align-items: flex-end;
            align-items: flex-end;
    bottom: 0;
    box-shadow: 40px 0 30px var(--gray-color-1);
    -webkit-flex: 1 1;
            flex: 1 1;
    position: fixed;
    width: 100%;
}

footer form {
    width: 100%;
}

footer input[type="text"] {
    background: var(--white-color);
    border: none;
    color: var(--gray-color-4);
    -webkit-flex-grow: 1;
            flex-grow: 1;
    font-size: 18px;
    padding: 20px;
    text-align: center;
    width: calc(100% - 40px);
}

footer input[type="text"]:focus {
    outline: none;
}

footer input[type="text"]::-webkit-input-placeholder {
    color: var(--gray-color-2);
    font-size: 12px;
    text-transform: uppercase;
}

footer input[type="text"]:-ms-input-placeholder {
    color: var(--gray-color-2);
    font-size: 12px;
    text-transform: uppercase;
}

footer input[type="text"]::placeholder {
    color: var(--gray-color-2);
    font-size: 12px;
    text-transform: uppercase;
}

button[type="submit"] {
    display: none;
}

.completed-status {
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    background: var(--gray-color-5);
    border-bottom: 1px solid var(--gray-color-1);
    border-top: 1px solid var(--gray-color-1);
    padding: 12px;
}

.completed-status p {
    color: var(--primary-color);
    font-size: 11px;
    margin: 0;
    text-transform: uppercase;
}

.completed-status span {
    font-size: 15px;
    font-weight: 600;
}

.list-wrapper {
    -webkit-align-self: center;
            align-self: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-direction: column;
            flex-direction: column;
    max-width: 700px;
    width: 100%;
}

.list-toolbar {
    padding: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

ol {
    background-color: var(--white-color);
    border: 1px solid var(--gray-color-6);
    box-shadow: 0 10px 10px -5px var(--gray-color-1);
    list-style: none;
    margin-bottom: 200px;
    margin-top: 0;
    padding-left: 0;
}

ol:empty {
    display: none;
}

li {
    -webkit-align-items: center;
            align-items: center;
    background-color: var(--white-color);
    border-bottom: 1px solid var(--gray-color-6);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    font-size: 18px;
    padding: 20px;
}

.list-item-content {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.list-item-content.completed {
    color: var(--gray-color-6);
    text-decoration: line-through;
}

.btn-complete {
    background: transparent;
    border: none;
    border-radius: 6px;
    color: var(--gray-color-7);
    cursor: pointer;
    font-size: 25px;
    font-weight: 600;
    line-height: 0;
}

.btn-complete:hover,
.btn-complete.completed {
    color: var(--green-color-1);
}

.btn-delete {
    background: transparent;
    border: none;
    color: var(--gray-color-7);
    cursor: pointer;
    font-size: 25px;
    font-weight: 600;
    line-height: 0;
}

.btn-delete:hover {
    color: var(--red-color-1);
}

.btn-delete-all {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
}

.btn-delete-all .btn-label {
    font-size: 12px;
    margin-left: 30px;
    position: absolute;
    visibility: hidden;
}
.btn-delete-all:hover .btn-label {
    visibility: visible;
}

.list-item {
    background-color: var(--white-color);
    height: 40px;
}

